  componentDidMount = async () => {
    try {
      // Get network provider and web3 instance.
      const web3 = await getWeb3();
      if (!window.web3) {
        //用来判断你是否安装了metamask
        window.alert("Please install MetaMask first."); //如果没有会去提示你先去安装
        return;
      }
      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // Get the contract instance.
      const networkId = await web3.eth.net.getId();
      if (networkId !== 137) {
        window.alert(`Please Use Polygon Network`);
      }

      const deployedNetwork = KOIProject.networks[137];
      const instance = new web3.eth.Contract(
        KOIProject.abi,
        deployedNetwork && deployedNetwork.address
      );

      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ web3, accounts, contract: instance }, this.runExample);
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`
      );
      console.error(error);
    }
  };

  runExample = async () => {
    const { accounts, contract, web3 } = this.state;
    const _GetNetworkId = await web3.eth.net.getId();

    // Stores a given value, 5 by default.
    // await contract.methods.Get_rich_All().send({ from: accounts[0] });

    // Get the value from the contract to prove it worked.
    //  const [lang, setLang] = useState("en");
    console.log("accounts:" + accounts);

    //  console.log(useState["en"]);

    this.setState({ GetNetworkId: _GetNetworkId });
    if (_GetNetworkId === 137) {
      var accountStr = accounts[0];
      accountStr = accountStr.substr(0, 6) + "..." + accountStr.substr(-6);

      this.setState({ NetWorkState: accountStr });
    } else {
      this.setState({ NetWorkState: "Connect wallet" });
    }

    const _currentPrice = await contract.methods.getPrice().call();
    const _Balance = await contract.methods.balanceOf(accounts[0]).call();
    const _KOI_Provenance = await contract.methods.KOI_Provenance().call();
    const _symbol = await contract.methods.symbol().call();
    const _totalSupply = await contract.methods.totalSupply().call();

    // Update state with the result.
    this.setState({ currentPrice: _currentPrice });
    this.setState({ Balance: _Balance });
    this.setState({ KOI_Provenance: _KOI_Provenance });
    this.setState({ symbol: _symbol });
    this.setState({ totalSupply: _totalSupply });

    // const _MaticBalance = await  accounts[0].getBalance()
    // this.setState({ MaticBalance: _MaticBalance});
  };

